<script>

import { mAuth, provider } from '@/db'
import { mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      // map `this.user` to `this.$store.getters.user`
      user: 'user'
    })
  },

  methods: {
    close () {
      this.dialog = false
    },

    validate () {
      if (this.$refs.form.validate()) {
        this.login()
      }
    },
    login: function () {
      mAuth.signInWithPopup(provider).then(function (result) {
        // This gives you a Google Access Token. You can use it to access the Google API.
        // var token = result.credential.accessToken
        // The signed-in user info.
        // var user = result.user
        // ...
        console.log(result.user.displayName)
      }).catch(function (error) {
        console.log(error.message)
        // Handle Errors here.
        // var errorCode = error.code
        // var errorMessage = error.message
      })
    },

    logout: function () {
      mAuth.signOut()
        .then(() => {
          this.$router.replace({
            name: 'Home'
          })
        })
    }
  },
  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  data: () => ({
    valid: true,
    username: '',
    usernameRules: [
      v => !!v || 'Username is required',
      v => (v && v.length <= 10) || 'Name must be less than 10 characters'
    ],
    password: '',
    show1: false,
    rules: {
      required: value => !!value || 'Required.',
      min: v => v.length >= 8 || 'Min 8 characters'
    },
    drawers: ['Default (no property)', 'Permanent', 'Temporary'],
    dialog: false,
    isDarkMode: !!localStorage.getItem('isDark'),
    primaryDrawer: {
      model: null,
      type: 'Permanent',
      clipped: true,
      floating: true,
      mini: false
    },
    footer: {
      inset: false
    },
    items: [
      { heading: 'Menu' },
      { icon: 'mdi-contacts', text: 'Home', url: '/' },
      { icon: 'mdi-cellphone-link', text: 'Medicaments', url: '/medicaments' },
      { icon: 'mdi-history', text: 'About', url: '/about' }
    ]
  })
}

</script>

